/********** CSS FLEXBOX **********/

.navbar {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  height: 50px;
  height: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  font-size: 1em;
  padding: 10px;
  -ms-flex-align: center;
      align-items: center; /* x-axis */

  /* justify-content: flex-end; */
  /* justify-content: space-evenly; */
  /* flex-direction: column; */
  /* background-color: grey; */
}

.unsplash {
  -ms-flex: 1 1;
      flex: 1 1;
  /* margin-right: auto; */
}

.navbar a {
  text-decoration: none;
  padding: 25px;
  color: grey;
}

.navbar a:hover {
  color: black;
}

.navbar a:active {
  color: #008eb2;
}
input {
  width: 300px;
  height: 30px;
  font-size: 1em;
  border-radius: 3px;
  border: 1px solid grey;
  text-align: center;
}

.unsplash > i {
  font-size: 2em;
}
/********** CSS GRID **********/
.wrapper {
  display: grid;
  /* grid-template-columns: repeat(12, 1fr); */
  /* grid-template-rows: 500px; */
  /* grid-template-areas:
    "l l l l l l l l l l l l"
    "o o o o o o o o o o o o "; */
}

.landing {
  grid-column: 1 / -1;
  /* grid-area: l; */
  padding: 40px;
}

.landing-text {
  text-align: center;
}

img {
  -o-object-fit: contain;
     object-fit: contain;
  width: 100%;
  height: 500px;
}

.content-one {
  grid-column: 1 / -1;
  /* grid-area: o; */

  /* Nested CSS Grid */
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 4fr));
  grid-gap: 15px;
  grid-auto-rows: 500px;
  grid-auto-flow: dense;
  padding: 40px;
  /* background-color: orange; */
}

.content-two {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 3fr));
  grid-gap: 15px;
  grid-auto-rows: 500px;
  /* grid-auto-flow: dense; */
  padding: 40px;
  /* background-color: yellow; */
}

/********** MEDIA **********/
@media all and (max-width: 400px) {
  .navbar > li {
    -ms-flex: 1 1 100%;
        flex: 1 1 100%;
  }

  .search {
    -ms-flex-order: 1;
        order: 1;
  }

  .content-two {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    grid-auto-rows: 200px;
  }
}



